@import url(https://fonts.googleapis.com/css?family=Lexend+Deca);
.top_panel {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  height: 50px;
  border-bottom: solid 2px #f0f0f0;
}
.top_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.top_row:nth-child(1) {
  width: 40%;
}
.top_row:nth-child(2) {
  width: 60%;
}
.top_cell {
  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-basis: 100%;
  height: 100%;
  padding: 5px 2.5px;
  text-align: center;
}
.top_cell:first-child {
  padding-left: 5px;
}
.top_cell:last-child {
  padding-right: 5px;
}
.top_button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #f0f0f0;
  border: none;
  border-radius: 5px;
  -webkit-transition: background 0.1s ease;
  transition: background 0.1s ease;
}
.top_button:hover {
  cursor: pointer;
  background: #e0e0e0;
}
@media screen and (max-width: 640px), screen and (max-height: 640px) {
  .top_button {
    padding: 5px 10px;
  }
}
@media screen and (max-width: 560px) {
  .top_panel {
    height: 100px;
  }
  .top_row {
    width: 100% !important;
  }
}

.glyph {
  width: 80px;
  height: 40px;
  margin: 10px 0;
}
.glyph[data-color="red"] {
  fill: #ef314d;
  stroke: #ef314d;
}
.glyph[data-color="green"] {
  fill: #4caf50;
  stroke: #4caf50;
}
.glyph[data-color="blue"] {
  fill: #3074d4;
  stroke: #3074d4;
}
@media screen and (max-width: 640px), screen and (max-height: 640px) {
  .glyph {
    width: 40px;
    height: 20px;
    margin: 5px 0;
  }
}

.card_button {
  display: flex;
  background: none;
  border: none;
  outline: none;
  border-radius: 10px;
  margin: 10px;
  padding: 0;
  overflow: visible;
}
.card_button:hover .card {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  cursor: pointer;
}
.card_button:hover .card,
.card_button:focus .card {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.card_button[data-selected='true'] .card {
  border-color: #000000;
}
.card_button[data-hinted='true'] {
  -webkit-animation: hint_glow 1s ease alternate infinite;
          animation: hint_glow 1s ease alternate infinite;
}
@-webkit-keyframes hint_glow {
  from {
    box-shadow: none;
  }
  to {
    box-shadow: 0 0 10px #9c27b0;
  }
}
@keyframes hint_glow {
  from {
    box-shadow: none;
  }
  to {
    box-shadow: 0 0 10px #9c27b0;
  }
}
.card {
  width: 120px;
  height: 200px;
  border-radius: 10px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border: solid 3px #e0e0e0;
  stroke-width: 4px;
  vertical-align: top;
  -webkit-transition: box-shadow 0.1s ease, -webkit-transform 0.1s ease;
  transition: box-shadow 0.1s ease, -webkit-transform 0.1s ease;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
  transition: transform 0.1s ease, box-shadow 0.1s ease, -webkit-transform 0.1s ease;
}
@media screen and (max-width: 640px), screen and (max-height: 640px) {
  .card_button {
    margin: 5px;
    border-radius: 5px;
  }
  .card {
    width: 60px;
    height: 100px;
    border-radius: 5px;
    border-width: 2px;
  }
}

.middle_panel {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 50px - 50px);
}
.card_container {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  max-width: 960px;
  max-height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 10px;
}
@media screen and (max-width: 560px) {
  .middle_panel {
    height: calc(100% - 100px - 50px);
  }
}

.bottom_panel {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  height: 50px;
  overflow-x: auto;
  overflow-y: hidden;
  border-top: solid 2px #f0f0f0;
}
.card_thumbnail {
  display: flex;
  position: relative;
}
.card_thumbnail:nth-child(3n + 1) {
  left: 16px;
  margin-left: -10px;
}
.card_thumbnail:nth-child(3n) {
  left: -16px;
  margin-right: -10px;
}
.card_thumbnail .card {
  width: 24px;
  height: 40px;
  border-radius: 2.5px;
  margin: 0;
  border-width: 1px;
}
.card_thumbnail .glyph {
  width: 16px;
  height: 8px;
  margin: 2.5px 0;
}

.game_over_panel {
  display: flex;
  position: fixed;
  justify-content: center;
  align-items: center;
  left: 0;
  top: 100px;
  right: 0;
  bottom: 100px;
  pointer-events: none;
  touch-action: none;
}
.game_over_panel svg {
  width: 320px;
}
.text_outline {
  fill: none;
  stroke: #000000;
  stroke-width: 5px;
  stroke-linecap: round;
  stroke-linejoin: round;
  font-size: 40pt;
}
.text_fill {
  fill: #ffffff;
  font-size: 40pt;
}
* {
  font-family: 'Lexend Deca', sans-serif;
}
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}
#root {
  height: 100%;
  overflow: hidden;
}
button {
  font-size: 1em;
}

@media screen and (max-width: 640px), screen and (max-height: 640px) {
  * {
    font-size: 10pt;
  }
}

